<template>
  <div id="app">    
    <keep-alive>
     <router-view v-if="$route.meta.keepAlive && isReloadAlive" />
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive && isReloadAlive"/>
  </div>
</template>

<script>
import { ToastFn } from "@/utils/common";

export default {
  name: "App",
  provide() {
    return {
      reload: this.reload
    };
  },
  data() {
    return {
      isReloadAlive: true
    };
  },
  created() {
    this.$router.beforeEach((to, from, next) => {
      // console.log(to);
      next();
    });
  },
  methods: {
    reload() {
      this.isReloadAlive = false
      this.$nextTick(function() {
        this.isReloadAlive = true
      })
    }
  }
};
</script>

<style>
html,
body,
#app {
  width: 100%;
  height: 100%;
}
</style>



