<template>
  <div class="uploadIdCard_div">
    
    <div class="top_div dsp_flex">
      <p>你好, <span>{{name}}</span></p>
      <p>请上传身份证完善身份信息</p>
    </div>

    <div class="upload_area_div">
      <div class="dsp_flex upload_div_wrapper" v-if="idCardIsShow == 1">
        <van-uploader :after-read="afterReadIdOnFn">
          <div v-if="!idCardOnUrl">
            <img src="../../../static/img/id_on.png" alt="" >
            <span>点击上传身份证人像面</span>
          </div>          
          <div v-else>
            <img :src="idCardOnUrl" alt="">
          </div>
        </van-uploader>
      </div>
      
      <div  class="dsp_flex upload_div_wrapper" v-else>
        <van-uploader :after-read="afterReadIdOffFn">
          <div v-if="!idCardOffUrl">
            <img src="../../../static/img/id_off.png" alt="" >
            <span>点击上传身份证国徽面</span>
          </div>          
          <div v-else>
            <img :src="idCardOffUrl" alt="" >
          </div>
        </van-uploader>
      </div>      
    </div>

    <div class="btn_div">
      <van-button class="btn" plain type="info" @click="uploadBtnClkFn">开始上传</van-button>      
    </div>
    
  </div>
</template>

<script>
import './index.css'
import { ToastFn, ToastLoadingFn } from "@/utils/common"
import { aliyunUploadFileApi } from "@/utils/api"
import { Toast } from 'vant'


export default {
  data(){
    return{
      name: '',
      idCardIsShow: null,
      idCardOnUrl: '',
      idCardOffUrl: ''
    }
  },
  created(){
    if(this.$route.query.id == 1){
      this.idCardIsShow = 1
    }else{
      this.idCardIsShow = 2
    }

    if(this.$route.query.name){
      this.name = this.$route.query.name
    }

    let $storage = window.sessionStorage

    if($storage.getItem("idCardOnUrlPath")){
      this.idCardOnUrl = $storage.getItem("idCardOnUrlPath")
    }

    if($storage.getItem("idCardOffUrlPath")){
      this.idCardOffUrl = $storage.getItem("idCardOffUrlPath")
    }
  },
  methods: {
    afterReadIdOnFn(file){
      this.afterRead(file, 1)
    },

    afterReadIdOffFn(file){
      this.afterRead(file, 2)
    },

    uploadBtnClkFn(){      
      let $storage = window.sessionStorage

      if(this.idCardIsShow == 1){ // 正面
        if(!this.idCardOnUrl){
          ToastFn('请上传身份证正面照')
          return
        }

        $storage.setItem('idCardOnUrlPath', this.idCardOnUrl)
      }else{ // 反面
        if(!this.idCardOffUrl){
          ToastFn('请上传身份证反面照')
          return
        }

        $storage.setItem('idCardOffUrlPath', this.idCardOffUrl)
      }      

      this.$router.go(-1);
    },

    afterRead(file, id){          
      let formData = new FormData()
      formData.append('file', file.file)

      ToastLoadingFn('上传中...', 0)
      aliyunUploadFileApi(formData).then(res => {
        ToastLoadingFn('上传中...', 10)

        let { code, msg, data } = res
        if(code === 10000){
          Toast.success('上传成功')

          if(id == 1){  // 正面
            this.idCardOnUrl = data
          }else{ // 反面
            this.idCardOffUrl = data
          }
        }else{
          ToastFn(msg)
        }
      })   
    } 
  }

}
</script>

<style>

</style>


