<template>
  <van-tabbar v-model="active"  @change="onChange" active-color="#2E343B">
    <van-tabbar-item v-for="(item,index) in footerTabData" :key="index">
      <span>{{item.txt}}</span>
      <template #icon="props">
        <img :src="props.active ? item.inactive : item.active" />
      </template>
    </van-tabbar-item>    
  </van-tabbar>
</template>

<script>
export default {
  props: {
    index: Number
  },
  data() {
    return {
      active: 0,
      footerTabData: [
        {
          txt: '首页',
          active: 'http://static.life.youlanw.com/index_icon_checked.png',
          inactive: 'http://static.life.youlanw.com/index_icon_default.png'
        },
        {
          txt: '我的',
          active: 'http://static.life.youlanw.com/my_icon_checked.png',          
          inactive: 'http://static.life.youlanw.com/my_icon_default.png'
        }
      ],     
    };
  },
  created(){
    if(this.index == 1){
      this.active = 1
    }else{
      this.active = 0
    }
  },
  methods: {
    onChange(index){      
      if(index == 0){
        this.$router.push({
          path: '/index'
        })
      }else{
        this.$router.push({
          path: '/userPage'
        })
      }
    }
  }
};
</script>

<style>
</style>


