// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // 在发送请求之前做些什么

  return config
}, function (error) {
  // 对请求错误做些什么

  return Promise.reject(error)
})

// 添加响应拦截器
axios.interceptors.response.use(function (response) {
  // 对响应数据做点什么
  let { code, message } = response.data
  if (code === 10018003 || code === 10018001) {

    // // 判断机型
    // setTimeout(() => {      
    //   // 判断当前内嵌H5页面所存在的宿主环境
    //   var u = navigator.userAgent
    //   var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // android终端或者uc浏览器
    //   var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
    //   if (isAndroid) {
    //     control.toLogin()
    //   } else if (isiOS) {
    //     window.location.href = 'ios://toLogin'
    //   }
    // }, 500)

  } else {
    return response
  }
},
  function (error) {
    // 对响应错误做点什么
    Toast({
      message: '哎呀,网络开了个小差~',
      position: 'bottom'
    })
  })

export default axios

// get
export function requestGet(url, params = {}) {
  return axios.get(url, { params: params }).then()
}

// post
export function requestPost(url, params = {}) {
  return axios.post(url, params, { emulateJson: true }).then()
}

// 图片上传OSS
export function requestNPost(url, params = {}) {
  return axios.post(url, params, { 
    headers: {
      'Content-Type': 'multipart/form-data'
    }
   }).then()
}


