<template>
  <div class="hello" :style="{ background: 'url(' + loginBg + ')' }">
    <div class="form_div">
      <van-cell-group class="vant_group_div">
        <van-field
          class="vant_ipt"
          v-model="idVal"
          label=""
          clearable
          placeholder="请输入身份证号"
        />

        <van-field
          class="vant_telIpt_div vant_ipt"
          type="tel"
          maxLength="11"
          v-model="telVal"
          label="+86"
          clearable
          placeholder="请输入手机号码"
        />

        <van-field
          class="getSmsCode_div vant_ipt"
          type="smsVal"
          maxLength="11"
          v-model="smsCodeVal"
          label=""
          clearable
          placeholder="请输入验证码"
        >
          <template #button>
            <van-button class="getSmsBtn" size="small" plain @click="getCodeBtnClkFn" :disabled="getCodeBtnStatus"
              >{{getCodeBtnTxt}}</van-button
            >
          </template>
        </van-field>
      </van-cell-group>

      <van-button class="loginBtn" plain @click="loginClkBtn">登录</van-button>

      <div class="user_tips_div dsp_flex">
        <van-checkbox
          v-model="userChecked"
          checked-color="#035BA3"
        ></van-checkbox>
        <p>点击登录表示您同意        
          <span @click="userAgreementClk">用户协议</span>、
          <span @click="userPrivacyClk">隐私权政策</span>          
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import "./login.css"
import { Toast } from 'vant'
import loginBg from "../../../static/img/login_bg.png"
import { payslipLoginApi, smsMobileApi } from "@/utils/api"
import { ToastFn, IdentityCodeValid, ToastLoadingFn } from '@/utils/common'

function ToastResetFn(msg, time){
  Toast({
    message: msg,
    position: 'bottom',
    duration: time
  })
}
 
export default {
  name: "login",
  data() {
    return {
      loginBg,
      idVal: null,
      telVal: null,
      smsCodeVal: null,
      userChecked: false,
      getCodeBtnStatus: false,
      getCodeBtnTxt: '获取验证码'
    };
  },
  created(){
    let $token = window.localStorage.getItem('hroToken')
    
    if($token){
      ToastResetFn('已有账号登录, 跳转主页...', 0)

      setTimeout(() => {
        this.$router.push({
          path: '/index'
        })
        ToastResetFn('已有账号登录, 跳转主页...', 20)
      },2000)    
    }
  },
  mounted(){

  },
  deactivated () { //清除keep-alive的缓存
    if(this.$route.query.no_cache){
      if(this.$route.query.no_cache == 1){
        this.$destroy(true)
      }      
    }
  },
  methods: {
    getCodeBtnClkFn(){
      if(!this.telVal){
        ToastFn('请输入手机号码')
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.telVal)) {
          ToastFn("手机号码不正确")
          return
        }     
      }
      
      let _this = this            
      let params = {
        mobile: this.telVal
      }

      smsMobileApi(params).then(res => {
        let { code, msg } = res

        if(code === 10000){
          var count = 60;
          var timer = setInterval(() => {
            count--;
            _this.getCodeBtnTxt = count + "s";
            _this.getCodeBtnStatus = true; // disabled
            if (count === 0) {
              _this.getCodeBtnTxt = "获取验证码";
              _this.getCodeBtnStatus = false; 
              clearInterval(timer);
            }
          }, 1000);          
          Toast.success('验证码已发送')          
        }else{          
          ToastFn(msg)
        }
      })
    },
    
    loginClkBtn() {
      if(!this.idVal){
        ToastFn('请输入身份证号')
        return
      }else{        
        if(!IdentityCodeValid(this.idVal)){
          return false
        }
      } 
      
      if(!this.telVal){
        ToastFn('请输入手机号码')
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.telVal)) {
          ToastFn("手机号码不正确")
          return
        }        
      }

      if(!this.smsCodeVal){
        ToastFn("请输入验证码")
        return
      } 
      
      if(!this.userChecked){
        ToastFn("请勾选用户协议以及隐私权政策")
        return
      }

      let params = {
        mobile: this.telVal,
        documentMobile: this.idVal, // 522225199210017816
        code: this.smsCodeVal,
      };

      payslipLoginApi(params).then((res) => {
        let { code, msg, data } = res;
        if (code === 10000) { 
          window.localStorage.setItem('hroToken', data)

          Toast.success('登录成功')
          
          setTimeout(() => {
            this.$router.push({
              path: '/index'
            })
          }, 2000)
        } else {
          ToastFn(msg)
        }
      });
    },

    userAgreementClk() {
      this.$router.push(
        {
          path: '/userAgreement'
        }
      )
    },

    userPrivacyClk() {
       this.$router.push(
        {
          path: '/UserPrivacy'
        }
      )
    },

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>


