<template>
  <div class="userDetail_div">
    
    <div class="top_div">
      <p class="title">请完善个人基础信息</p>
      <p class="name_p">{{name}}</p>
      <p class="idInfo_p">
        <span>证件号码: </span>
        <span>{{idNum}}</span>
      </p>
    </div>

    <div class="userDetail_cnt_div">
      <div class="userDetailList_div">
        <span class="necessary">手机号码</span>
        <van-cell-group>
          <van-field v-model="telVal" type="tel" clearable label="" maxlength="11" placeholder="请输入手机号" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>户籍地址</p>
        <van-cell-group>
          <van-field v-model="addressVal" clearable label="" placeholder="请输入户籍地址" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="nationListClkFn">
        <p>民族</p>
        <van-cell-group>
          <van-field v-model="nationVal" :class="{'active': nationVal != '请选择民族'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>现住址</p>
        <van-cell-group>
          <van-field v-model="currentAddressVal" clearable label="" placeholder="请输入现地址" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div"  @click="maritalListClkFn">
        <p>婚姻状况</p>
        <van-cell-group>
          <van-field v-model="maritalStatusVal" :class="{'active': maritalStatusVal != '请选择婚姻状况'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>Email</p>
        <van-cell-group>
          <van-field v-model="emailVal" clearable label=""  placeholder="请输入邮箱" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="degreeListClkFn">
        <p>学历</p>
        <van-cell-group>
          <van-field v-model="degreeVal" :class="{'active': degreeVal != '请选择学历'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>毕业院校</p>
        <van-cell-group>
          <van-field v-model="graduateSchoolVal" clearable label=""  placeholder="请输入毕业院校" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="graduateListClkFn">
        <p>毕业时间</p>
        <van-cell-group>
          <van-field v-model="graduateDateVal" :class="{'active': graduateDateVal != '请选择毕业时间'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="registerStatusListClkFn">
        <p>户口性质</p>
        <van-cell-group>
          <van-field v-model="registerStatusVal" :class="{'active': registerStatusVal != '请选择户口性质'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="politicsStatusListClk">
        <p>政治面貌</p>
        <van-cell-group>
          <van-field v-model="politicsStatusVal" :class="{'active': politicsStatusVal != '请选择政治面貌'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary">紧急联系人</span>
        <van-cell-group>
          <van-field v-model="urgencyContactsVal" clearable label="" placeholder="请输入紧急联系人" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary">紧急联系人电话</span>
        <van-cell-group>
          <van-field v-model="urgencyContactsTelVal" clearable label="" type="tel" maxlength="11" placeholder="请输入紧急联系人电话" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class=" necessary">紧急联系人地址</span>
        <van-cell-group>
          <van-field v-model="urgencyContactsAddressVal" clearable label=""  placeholder="请输入紧急联系人地址" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary">紧急联系人关系</span>
        <van-cell-group>
          <van-field v-model="urgencyContactsRelationVal" clearable label=""  placeholder="请输入紧急联系人关系" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary">银行账号</span>
        <van-cell-group>
          <van-field v-model="bankNumVal" type="number" clearable label="" @blur="bankNumBlurFn"  placeholder="请输入银行账号" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <span class="necessary">开户行</span>
        <van-cell-group>
          <van-field v-model="openBankVal" :class="{'active': openBankVal != '系统自动识别开户行'}" disabled clearable label="" placeholder="" />          
        </van-cell-group>        
      </div>

      <div class="userDetailList_div">
        <p>身份证正面照</p>
        <van-cell-group class="upload_div">
          <van-field v-model="idCardOnPathVal" clearable disabled label="" placeholder="请上传身份证正面照" />
          
          <div class="uploadIcon" @click="idCardOnIconClkFn(1)">
            <van-button :icon="cameraIcon" plain></van-button>        
          </div>
        </van-cell-group>      
      </div>

      <div class="userDetailList_div">
        <p>身份证反面照</p>
        <van-cell-group class="upload_div">
          <van-field v-model="idCardOffPathVal" clearable disabled label="" placeholder="请上传身份证反面照" />

          <div class="uploadIcon" @click="idCardOnIconClkFn(2)">
            <van-button :icon="cameraIcon" plain></van-button>
          </div>
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>学历证书</p>
        <van-cell-group class="upload_div">
          <van-field v-model="academicCertificatePathVal" clearable disabled label="" placeholder="请上传学历证书正面照" />

          <van-uploader class="uploadIcon" :after-read="afterReadXl">
            <van-button :icon="cameraIcon" plain></van-button>
          </van-uploader>
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>学位证书</p>
        <van-cell-group class="upload_div">
          <van-field v-model="academicDegreePathVal" clearable disabled label="" placeholder="请上传学历证书反面照" />

          <van-uploader class="uploadIcon" :after-read="afterReadXw">
            <van-button :icon="cameraIcon" plain></van-button>
          </van-uploader>
        </van-cell-group>
      </div>

      <div class="userDetailList_div" @click="workDateListClkFn">
        <p>参加工作时间</p>
        <van-cell-group>
          <van-field v-model="workDateVal" :class="{'active': workDateVal != '请选择参加工作时间'}" clearable label="" disabled  right-icon="arrow" placeholder="" />
        </van-cell-group>
      </div>

      <div class="userDetailList_div">
        <p>工作经历</p>
        <van-cell-group>
          <van-field v-model="workExperienceVal" type="textarea" autosize clearable label=""  placeholder="请输入工作经历" />
        </van-cell-group>
      </div>

      <div class="footer_btn_div dsp_flex">
        <van-button plain class="ensure_btn btn" @click="ensureBtnClkFn">保存</van-button>
        <van-button plain class="cancel_btn btn" @click="cancelBtnClkFn">取消</van-button>
      </div>

    </div>
    
    <!-- 组件picker -->
    <van-popup v-model="showPicker" round position="bottom" >
      <van-picker      
        title=""        
        show-toolbar 
        value-key="name"     
        :columns="columns"
        @confirm="onConfirm"
        @cancel="showPicker = false"            
      />
    </van-popup>

    <!-- 日期组件 -->
    <van-popup v-model="showDatePicker" round position="bottom" >
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择年月日"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="datePickerOnConfirm"
        @cancel="showDatePicker = false"
      />
    </van-popup>

    
  </div>
</template>

<script>
import './index.css'
import { formatTime, ToastFn, ToastLoadingFn } from "@/utils/common"
import { getBankCardNameApi, saveEmployeeUserApi, aliyunUploadFileApi, isTokenApi } from "@/utils/api"
import { 
  nationData, 
  maritalStatusData, 
  degreeData, 
  registerStatusData, 
  politicsStatusData,   
} from '@/utils/json.json'
import { Toast } from 'vant'

export default {
  data(){
    return{
      name:'',
      idNum: null,
      cameraIcon: 'http://static.life.youlanw.com/camera.png',
      telVal: null,
      addressVal: '',
      nationVal: '请选择民族',
      currentAddressVal: '',
      maritalStatusVal: '请选择婚姻状况',
      maritalStatusCode: null,
      emailVal: '',
      degreeVal: '请选择学历',
      degreeCode: null,
      graduateSchoolVal:'',
      graduateDateVal: '请选择毕业时间',
      registerStatusVal:'请选择户口性质',
      registerStatusCode: null,
      politicsStatusVal: '请选择政治面貌',
      politicsStatusCode: null,
      urgencyContactsVal: '',
      urgencyContactsTelVal: null,
      urgencyContactsAddressVal: '',
      urgencyContactsRelationVal: '',
      openBankVal: '系统自动识别开户行',
      bankNumVal: null,
      idCardOnPathVal: '',
      idCardOffPathVal: '',
      academicCertificatePathVal:'',
      academicDegreePathVal: '',
      workDateVal:'请选择参加工作时间',
      workExperienceVal: '',                        
      showPicker: false, 
      columns: [], 
      pickerSetId: null,
      showDatePicker: false,
      minDate: new Date(1970, 0, 1),
      maxDate: new Date(),    
      currentDate: new Date(),
      datePickerSetId: null,
      userInfoArr: [],      
    }
  },
  created(){        
    if(this.$route.query.userInfo){
      let _data = JSON.parse(this.$route.query.userInfo)

      this.idNum = _data.idNum
      this.name = _data.name
    }

    let $storage = window.sessionStorage
    let _storage = window.localStorage
    if($storage.getItem("idCardOnUrlPath")){
      this.idCardOnPathVal = $storage.getItem("idCardOnUrlPath")      
    }

    if($storage.getItem("idCardOffUrlPath")){
      this.idCardOffPathVal = $storage.getItem("idCardOffUrlPath")
    }    

    if(_storage.getItem('hroToken')){      
      this.getUserInfoIvkFn(_storage.getItem('hroToken'))
    }
  },

  computed: {
  
  },

  methods: {
    getUserInfoIvkFn(token){            
      let params = {
        token
      }      

      ToastLoadingFn('加载中...', 0)
      isTokenApi(params).then(res => {
        ToastLoadingFn('加载中...', 10)

        let {code, data, msg} = res

        if(code === 10000){
          this.userInfoArr = data
          
          let { 
            mobile, 
            permanentAddress, 
            nation, 
            currentAddress, 
            maritalStatus,
            email,
            education,
            graduateSchool,
            graduationTimeString,
            registeredResidenceType,
            politicalOutlook,
            emergencyContactName,
            emergencyContactMobile,
            emergencyContactAddress,
            emergencyContactRelationship,
            bankOfDeposit,
            bankAccount,
            cardFrontPath,
            cardBackPath,
            academicCertificatePath,
            academicDegreeCertificatePath,
            workingHoursTimeString,
            workExperience
            } = data

          this.telVal = mobile // 手机号
          this.addressVal = permanentAddress // 户籍地址
          this.nationVal = nation // 民族
          this.currentAddressVal = currentAddress // 现住址
          this.maritalStatusCodeToName(maritalStatus) // 婚姻状况
          this.emailVal = email // 邮件
          this.degreeCodeToName(education) // 学历
          this.graduateSchoolVal = graduateSchool, // 毕业学校          
          this.graduateDateValFormat(graduationTimeString) // 毕业时间                  
          this.registerStatusCodeToName(registeredResidenceType) // 户口性质      
          this.politicsStatusCodeToName(politicalOutlook) // 政治面貌
          this.urgencyContactsVal = emergencyContactName // 紧急联系人
          this.urgencyContactsTelVal = emergencyContactMobile // 紧急联系人电话
          this.urgencyContactsAddressVal = emergencyContactAddress  // 紧急联系人地址
          this.urgencyContactsRelationVal = emergencyContactRelationship  // 紧急联系人关系
          this.openBankVal = bankOfDeposit // 开户行
          this.bankNumVal = bankAccount // 银行账号
          
          if(cardFrontPath){
            this.idCardOnPathVal = cardFrontPath // 身份证正面照
          }

          if(cardBackPath){
            this.idCardOffPathVal = cardBackPath // 身份证反面照
          }

          if(academicCertificatePath){
            this.academicCertificatePathVal = academicCertificatePath // 学历证书
          }
          
          if(academicDegreeCertificatePath){
            this.academicDegreePathVal = academicDegreeCertificatePath  // 学位证书
          }                  
          
          this.workDateValFormat(workingHoursTimeString)  // 参加工作时间

          this.workExperienceVal = workExperience // 工作经历
        }else{
          ToastFn(msg)
        }
      })
    },    
    
    maritalStatusCodeToName(code){      
      maritalStatusData.forEach(item => {        
        if(item.id == code){          
          this.maritalStatusVal = item.name          
        }
      })    
    },

    degreeCodeToName(code){
      degreeData.forEach(item => {        
        if(item.id == code){          
          this.degreeVal = item.name          
        }
      })  
    },

    registerStatusCodeToName(code){
      registerStatusData.forEach(item => {        
        if(item.id == code){          
          this.registerStatusVal = item.name          
        }
      })   
    },

    politicsStatusCodeToName(code){
      politicsStatusData.forEach(item => {        
        if(item.id == code){          
          this.politicsStatusVal = item.name          
        }
      })  
    },

    graduateDateValFormat(str){      
      if(!str){
        this.graduateDateVal = '请选择毕业时间'
      }else{
        if(str.indexOf(' ') > -1){
          this.graduateDateVal = str.substring(str.indexOf(' '), 0)
        }        
      }
    },

    workDateValFormat(str){
       if(!str){
        this.workDateVal = '请选择参加工作时间'
      }else{
        if(str.indexOf(' ') > -1){
          this.workDateVal = str.substring(str.indexOf(' '), 0)
        }        
      }
    },

    nationListClkFn(){      
      this.showPicker = true
      this.columns = nationData
      this.pickerSetId = 1
    },
    
    maritalListClkFn(){      
      this.showPicker = true 
      this.columns = maritalStatusData
      this.pickerSetId = 2
    },

    degreeListClkFn(){
      this.showPicker = true 
      this.columns = degreeData
      this.pickerSetId = 3      
    },

    registerStatusListClkFn(){
      this.showPicker = true 
      this.columns = registerStatusData
      this.pickerSetId = 4     
    },

    politicsStatusListClk(){
      this.showPicker = true 
      this.columns = politicsStatusData
      this.pickerSetId = 5     
    },

    graduateListClkFn(){
      this.showDatePicker = true
      this.datePickerSetId = 1
    },
    
    workDateListClkFn(){
      this.showDatePicker = true
      this.datePickerSetId = 2
    },

    datePickerOnConfirm(e){
      this.showDatePicker = false      

      let _date = formatTime(e)

      let $id = this.datePickerSetId
      if($id === 1){
        this.graduateDateVal = _date
      }else if($id === 2){
        this.workDateVal = _date
      }
    },
    
    onConfirm(e){      
      this.showPicker = false
      
      let $id = this.pickerSetId
      if($id === 1){
        this.nationVal = e.name
      }else if($id === 2){
        this.maritalStatusVal = e.name
        this.maritalStatusCode = e.id
      }else if($id === 3){
        this.degreeVal = e.name
        this.degreeCode = e.id
      }else if($id === 4){
        this.registerStatusVal = e.name
        this.registerStatusCode = e.id
      }else if($id === 5){
        this.politicsStatusVal = e.name
        this.politicsStatusCode = e.id
      }

    },

    bankNumBlurFn(){
      let validateBankCardReg = /^([1-9]{1})(\d{14}|\d{18})$/;

      if(!this.bankNumVal){
        ToastFn('请输入银行卡号')
        this.openBankVal = '系统自动识别开户行'
        return
      }else{
        if(!validateBankCardReg.test(this.bankNumVal)){
          ToastFn('请输入正确的银行卡号')
          return
        }
      }
      

      let params = {
        idCard: this.bankNumVal
      }
      
      getBankCardNameApi(params).then(res => {
        let { code, msg, data } = res 
        
        if(data != '无法识别银行卡所属银行名称'){
          this.openBankVal = data          
        }else{
          ToastFn(data)          
        }
      })            
    },

    idCardOnIconClkFn(index){
      this.$router.push({
        path: '/uploadIdcard',
        query: {
          id: index,
          name: this.name
        }
      })
    },

    afterReadXl(file){
      this.afterRead(file, 1)
    },

    afterReadXw(file){
      this.afterRead(file, 2)
    },

    afterRead(file, id){      
      let formData = new FormData()
      formData.append('file', file.file)

      ToastLoadingFn('上传中...', 0)
      aliyunUploadFileApi(formData).then(res => {
        ToastLoadingFn('上传中...', 10)

        let { code, msg, data } = res
        if(code === 10000){
          Toast.success('上传成功')

          if(id == 1){  // 学历
            this.academicCertificatePathVal = data
          }else{ // 学位
            this.academicDegreePathVal = data
          }
        }else{
          ToastFn(msg)
        }
      })
        
      // 图片上传OSS 设置 header
      // this.$http.post('/api/perfectUser/alyUploadFile', formData,{
      //   headers: {
      //     'Content-Type': 'multipart/form-data'
      //   }
      // }).then(res => {
      //   console.log(res)
      // })
    },

    ensureBtnClkFn(){            
      let validateBankCardReg = /^([1-9]{1})(\d{14}|\d{18})$/;

      if(!this.telVal){
        ToastFn('请输入手机号')
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.telVal)) {
          ToastFn("手机号码不正确")
          return
        }    
      }

      if(!this.emailVal){

      }else{
        if(!/^\w+@[a-z0-9]+\.[a-z]{2,4}$/.test(this.emailVal)){
          ToastFn("邮箱格式不正确")
          return
        }
      }

      if(!this.urgencyContactsVal){
        ToastFn("请输入紧急联系人")
        return
      }

      if(!this.urgencyContactsTelVal){
        ToastFn("请输入紧急联系人手机号")
        return
      }else{
        if (!/^1[3456789]\d{9}$/.test(this.urgencyContactsTelVal)) {
          ToastFn("紧急联系人手机号码不正确")
          return
        }   
      }

      if(!this.urgencyContactsAddressVal){
        ToastFn("请输入紧急联系人地址")
        return
      }

      if(!this.urgencyContactsRelationVal){
        ToastFn("请输入紧急联系人关系")
        return
      }

      if(!this.bankNumVal){
        ToastFn('请输入银行卡号')        
        return
      }else{
        if(!validateBankCardReg.test(this.bankNumVal)){
          ToastFn('请输入正确的银行卡号')
          return
        }
      }

      let params = {
        documentMobile: this.idNum, // Id 
        mobile: this.telVal,  // 手机号
        permanentAddress: this.addressVal, // 户籍地址
        nation: this.nationVal == '请选择民族' ? null : this.nationVal, // 民族
        currentAddress:  this.currentAddressVal, // 现住址
        maritalStatus: this.maritalStatusCode, // 婚姻状况
        email: this.emailVal, // 电子邮件
        education: this.degreeCode, // 学历
        graduateSchool: this.graduateSchoolVal, // 毕业学校
        graduationTimeStr:  this.graduateDateVal == '请选择毕业时间' ? null : this.graduateDateVal, // 毕业时间
        registeredResidenceType: this.registerStatusCode, // 户口性质
        politicalOutlook: this.politicsStatusCode, // 政治面貌
        emergencyContactName: this.urgencyContactsVal,  // 紧急联系人
        emergencyContactMobile: this.urgencyContactsTelVal, // 紧急联系人电话
        emergencyContactAddress: this.urgencyContactsAddressVal, // 紧急联系人地址
        emergencyContactRelationship: this.urgencyContactsRelationVal,  // 紧急联系人关系
        bankOfDeposit: this.openBankVal, // 开户行
        bankAccount: this.bankNumVal, // 银行账号
        cardFrontPath: this.idCardOnPathVal,  // 身份证正面照
        cardBackPath: this.idCardOffPathVal, // 身份证反面照
        academicCertificatePath: this.academicCertificatePathVal, // 学历证书
        academicDegreeCertificatePath: this.academicDegreePathVal,  // 学位证书
        workingHoursTimeStr: this.workDateVal == '请选择参加工作时间' ? null : this.workDateVal, // 参加工作时间' 
        workExperience: this.workExperienceVal // 工作经历
      }      

      saveEmployeeUserApi(params).then(res => {
        let { code, msg } = res

        if(code === 10000){
          Toast.success('保存成功')

          setTimeout(() => {
            this.$router.push({
              path: '/userPage'
            })
          },2000)
        }else{
          ToastFn(msg)
        }
      })
    },

    cancelBtnClkFn(){
      this.$router.push({
        path: '/userPage'
      })
    },
  }
}
</script>

<style scoped>

</style>


