<template>
  <div class="myPage_div">

    <div class="top_div">
      <div class="dsp_flex" @click="goDetail">
        <img src="../../../static/img/default_avatar.png" alt="" />
        <div class="user_info_div" v-if="token">
          <p>{{userData.name}}</p>
          <p>注册时间: {{userData.createTime | createTimeFilter}}</p>
        </div>

        <div v-else>
          <van-button class="login_btn" plain type="primary" @click="loginClk">请登录</van-button>
        </div>
      </div>
    </div>

    <div class="cnt_list">
      <ul>
        <li class="list dsp_flex" v-for="(item, index) in listData" :key="index" @click="listClkFn(index)">
          <img :src="item.path" alt="">
          <p>{{item.text}}</p>
        </li>        
      </ul>
    </div>

    <p class="quit_p" @click="quitClkFn" v-if="token">退出登录</p>

    <!-- 底部导航 -->
    <Component-footertab :index="tanIndex"></Component-footertab>
  </div>
</template>

<script>
import "./index.css"
import {signOutApi, isTokenApi } from '@/utils/api'
import { ToastFn, ToastLoadingFn } from '@/utils/common'
import { Toast } from 'vant'

export default {
  data() {
    return {
      tanIndex: null,
      listData:[
        {
          path: '../../static/img/privacyList_icon.png',
          text: '隐私权政策'
        },
        {
          path: '../../static/img/userAgreementList_icon.png',
          text: '用户协议'
        },
        {
          path: '../../static/img/aboutUsList_icon.png',
          text: '关于我们'
        }
      ],
      token: '',
      userData: []
    };
  },
  created() {
    if (this.$route.path == "/userPage") {
      this.tanIndex = 1;
    }   

    let _token = window.localStorage.getItem('hroToken')        
    if(_token){
      this.token = _token

      this.getUserInfo()
    }          
  },
  filters:{
    createTimeFilter(str){
      if(str){        
        if(str.indexOf(' ') > -1){
          return str.substring(str.indexOf(' '), 0)       
        }        
      }      
    }
  },
  methods: {
    listClkFn(index){
      if(index === 0){
        this.$router.push({
          path: '/UserPrivacy'
        })
      }else if(index === 1){
        this.$router.push({
          path: '/userAgreement'
        })
      }else{
        this.$router.push({
          path: '/aboutUs'
        })
      }
    }, 

    loginClk(){
      this.$router.push({
        path: '/login'
      })
    },
    
    getUserInfo(){
      let params = {
        token: this.token
      }

      ToastLoadingFn('加载中', 0)
      isTokenApi(params).then(res => {
        ToastLoadingFn('加载中', 10)

        let { code, msg, data } =  res

        if(code === 10000){
          this.userData = data
        }else{
          ToastFn(msg)

          this.token = ''

          setTimeout(() => {
            this.$router.push({
              path: '/login'
            })
          },2000)
        }
      })    
    },  

    quitClkFn(){
      let params = {
        token: this.token
      }

      signOutApi(params).then(res => {
        let { code, msg } = res
        
        if(code === 10000){ 
          ToastFn(msg)

          window.localStorage.removeItem('hroToken')

          setTimeout(() => {
            this.$router.push({
              path: '/login',
              query:{
                'no_cache': 1
              }
            })
          },2000)
        }else{
          ToastFn(msg)
        }
      })
    },
    
    goDetail(){
      let _data = {
        name: this.userData.name,
        idNum: this.userData.documentMobile
      }

      this.$router.push({
        path: '/userDetail',
        query:{
          userInfo: JSON.stringify(_data)
        }
      })

      let $storage = window.sessionStorage
      $storage.removeItem("idCardOnUrlPath")
      $storage.removeItem("idCardOffUrlPath")  
    }
  }
};
</script>

<style scoped>
</style>


