<template>
  <div class="index_div">

    <div class="search_div">
      <van-search
        v-model="searchVal"
        show-action
        label=""
        clearable
        shape="round"
        background="#F5F5F5"
        placeholder="选企业"
        @search="onSearch"
      >
        <template #action>
          <div class="searchTxt" @click="onSearch">搜索</div>
        </template>
      </van-search>
    </div>

    <div class="cnt_div" :style="{background: 'url(' + cntBg + ')'}">
      <!-- 有企业列表 -->
      <div class="list_div" v-if="listData.length > 0">
        <ul>
          <li v-for="(item, index) in listData" :key="index">
            <div class="top_div" @click.stop="listClkFn(index)">
              <span>{{item.companyName}}</span>
              <!-- <img :src="'../../../static/img/' + item.flag ? 'up' : 'down' +'_icon.png'" alt="">               -->
              <img src="../../../static/img/up_icon.png" alt="" v-if="item.flag"> 
              <img src="../../../static/img/down_icon.png" alt="" v-else>   
            </div>

            <div class="bottom_div unfold_div dsp_flex" v-show="!item.flag">
              <div class="datePicker_div" :class="{'datePicker_div_active': date != '请选择查询时间'}" @click.stop="datePickerClkFn">{{date}}</div>
              <van-button class="search_btn" plain type="info" color="#035BA3" @click.stop="dateRangeClkFn(item.projectInvoiceId)">查询</van-button>
            </div>

            <div class="search_box" v-show="searchIsClk">              

              <!-- 顶部三角 -->
              <i class="before"></i><i class="after"></i>

              <!-- 有工资页面 -->
              <div class="hasSalary_div" v-if="salaryDataArr.length > 0">

                <!-- 每月薪资明细 -->
                <div class="preMonthSalary_detail_div" v-for="(item, index) in salaryDataArr" :key='index'>
                  <div class="salary_list_div">
                    <div class="salary_list_top dsp_flex" @click.stop="preMouthListClkFn(index)">
                      <p>
                        <span class="month_span">{{item.issueDate | monthFilter}}月/</span>
                        <span>{{item.issueDate | yearFilter}}年</span>
                      </p>

                      <div class="salary_list_top_right">
                        <p :class="{'active_yellow': !item.flag}">明细</p>
                        <img src="../../../static/img/up_icon.png" alt="" v-if="item.flag">
                        <img src="../../../static/img/down_icon.png" alt="" v-else>
                      </div>                      
                    </div>
                  
                    <div>
                      <p class="shouldSendSalary_p list_p" v-if="item.shouldHairSalary">
                        <span>应发薪资</span>
                        <span>￥{{item.shouldHairSalary}}</span>
                      </p>
                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.otherAfterTax && !item.flag">
                        <span>税后其他</span>
                        <span>￥{{item.otherAfterTax}}</span>
                      </p>                     
                    </div>                 
                    

                    <div>
                      <p class="withholdAndRemitTax_p list_p">
                        <span>代扣代缴</span>
                        <span>- ¥ {{item.withholdingAndPayment}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.socialSecuritySalary && !item.flag">
                        <span>社保</span>
                        <span>-￥{{item.socialSecuritySalary}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.accumulationFundSalary && !item.flag">
                        <span>公积金</span>
                        <span>-￥{{item.accumulationFundSalary}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.personalIncomeTaxSalary && !item.flag">
                        <span>个税</span>
                        <span>-￥{{item.personalIncomeTaxSalary}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.mealCardDeduction && !item.flag">
                        <span>饭卡扣款</span>
                        <span>-￥{{item.mealCardDeduction}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.dormitoryDeduction && !item.flag">
                        <span>宿舍扣款</span>
                        <span>-￥{{item.dormitoryDeduction}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.loan && !item.flag">
                        <span>借款</span>
                        <span>-￥{{item.loan}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.rentDormitory && !item.flag">
                        <span>宿舍房租</span>
                        <span>-￥{{item.rentDormitory}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.waterAndElectricity && !item.flag">
                        <span>宿舍水电费</span>
                        <span>-￥{{item.waterAndElectricity}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.microLending && !item.flag">
                        <span>微借贷</span>
                        <span>-￥{{item.microLending}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.unionFees && !item.flag">
                        <span>工会费</span>
                        <span>-￥{{item.unionFees}}</span>
                      </p>

                      <p class="shouldSendSalary_p list_p fold_p" v-if="item.otherDeductions && !item.flag">
                        <span>其他扣款</span>
                        <span>-￥{{item.otherDeductions}}</span>
                      </p>

                    </div>                    
                  </div>

                  <div class="salaryTotal_div">
                    <span>实发￥{{item.paidSalary}}</span>
                  </div>                  
                </div>                    

                <!-- 合计 -->
                <!-- <div class="total_div">
                  
                  <div class="salary_list_div total_list_div">
                    <div class="salary_list_top dsp_flex">
                      <p>
                        <span class="month_span total_span">合计</span>                      
                      </p>

                      <div class="salary_list_top_right">
                        <p>明细</p>
                        <img src="../../../static/img/up_icon.png" alt="">
                      </div>                      
                    </div>

                    <p class="shouldSendSalary_p list_p">
                      <span>应发薪资</span>
                      <span>￥{{item.shouldHairSalary}}</span>
                    </p>                   

                    <p class="withholdAndRemitTax_p list_p">
                      <span>代扣代缴</span>
                      <span>- ¥ xxx</span>
                    </p>
                  </div>

                  <div class="salaryTotal_div">
                    <span>实发￥xxx</span>
                  </div>

                </div>                      -->
              </div>

              <!-- 无工资页面 -->
              <div class="noData_div dsp_flex" v-else>
                <img class="blank_img" src="../../../static/img/blank_img.png" alt="">
                <p>还没有工资哦~</p> 
              </div> 

            </div>                      
          </li>
        </ul>

      </div>

      <!-- 暂无数据 -->
      <div class="noData_div" v-else>
        <img class="blank_img" src="../../../static/img/blank_img.png" alt="">
        <p>还没有企业哦~</p> 
      </div>
    </div>

    <!-- 日历插件 -->
    <van-calendar v-model="dateShow" @confirm="onConfirm" type="range" :min-date="minDate" :max-date="maxDate" />

    <!-- 底部导航 -->
    <Component-footertab></Component-footertab>
  </div>
</template>

<script>
import './index.css'
import cntBg from '../../../static/img/index_bg.png'
import { searchEnterpriseListApi, searchSalaryInfoListApi } from '@/utils/api'
import { ToastFn, formatNumber, replaceRegFn } from '@/utils/common' 
import Vue from 'vue'


export default {
  data() {
    return {
      cntBg,
      searchVal: '',
      listData: [],
      dateShow: false, 
      minDate: new Date(2000,0,1),
      maxDate: new Date(), 
      date: '请选择查询时间', 
      token: '',
      startDate: '', 
      endDate: '',
      salaryDataArr: [],
      searchIsClk: false          
    };
  },  
  mounted(){
    this.token = window.localStorage.getItem('hroToken')

    this.getList()  
  }, 

  filters:{
    monthFilter(str){
      let _date =new Date(str)

      return _date.getMonth() + 1
    },

    yearFilter(str){
      let _date =new Date(str)

      return _date.getFullYear()
    }
  },

  methods: {
    preMouthListClkFn(idx){
      let _data = this.salaryDataArr      
      let preListData = _data[idx]

      preListData.flag = !preListData.flag  
          
      this.$forceUpdate() 
    },

    onSearch(){
      this.getList()
    },

    getList(){
      let params = {
        token: this.token,        
        search: this.searchVal
      }

      searchEnterpriseListApi(params).then(res => {
        let { code, data, msg } = res
        if(code === 10000){
          this.listData = data          

          let listDataArr = []

          this.listData.forEach(item => {
            item['flag'] = true            
            listDataArr.push(item)
          })

          this.listData = listDataArr                   
        }else{
          ToastFn(msg)

          setTimeout(() => {
            this.$router.push({
              path: '/login'
            })
          },2000)
        }
        
      })
    },

    formatDate(date) {
      return `${date.getFullYear()}/${formatNumber(date.getMonth() + 1)}/${formatNumber(date.getDate())}`;
    },

    datePickerClkFn(){      
      this.dateShow = true
    },   

    onConfirm(date){
      const [start, end] = date
      this.dateShow = false
      this.date = `${this.formatDate(start)} - ${this.formatDate(end)}`        
      
      this.startDate = replaceRegFn(this.formatDate(start))            
      this.endDate = replaceRegFn(this.formatDate(end))
    },   

    listClkFn(idx){      
      this.date = '请选择查询时间'

      this.searchIsClk = false

      let _data = this.listData      
      let preListData = _data[idx]

      preListData.flag = !preListData.flag  
          
      this.$forceUpdate()  
    },

    dateRangeClkFn(id){         
      if(this.date == '请选择查询时间'){
        ToastFn('请选择查询时间')        
      }else{
        this.searchIsClk = true

        let params = {
          token: this.token,
          projectInvoiceId: id,
          issueStartDate: this.startDate,
          issueEndDate: this.endDate
        }

        searchSalaryInfoListApi(params).then(res => {
          let { code, msg, data } = res

          if(code === 10000){
            this.salaryDataArr = data  

            let _dataArr = []
            this.salaryDataArr.forEach(item => {
              item['flag'] = true            
              _dataArr.push(item)
            })

            this.salaryDataArr = _dataArr    
          }else{
            ToastFn(msg)
          }
        })
      }
    },
  }
};
</script>

<style scoped>

</style>


