var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"userAgreement_div"},[_c('p',[_vm._v("\n\t\t一、总则 "),_c('br'),_vm._v("\n    1．1 用户应当同意本协议的条款并按照页面上的提示完成全部的注册程序。用户在进行注册程序过程中点击\"同意\"按钮即表示用户与优蓝招聘达成协议，完全接受本协议项下的全部条款。"),_c('br'),_vm._v(" \n    1．2 用户注册成功后，优蓝招聘将给予每个用户一个用户账号，该用户账号由用户负责保管；用户应当对以其用户账号进行的所有活动和事件负法律责任。"),_c('br'),_vm._v(" \n    1．3 用户一经注册优蓝招聘账号，除非子频道要求单独开通权限，用户有权利用该账号使用优蓝招聘各个频道的单项服务，当用户使用优蓝招聘各单项服务时，用户的使用行为视为其对该单项服务的服务条款以及优蓝招聘在该单项服务中发出的各类公告的同意。"),_c('br'),_vm._v(" \n    1．4 优蓝招聘会员服务协议以及各个频道单项服务条款和公告可由优蓝招聘随时更新，且无需另行通知。您在使用相关服务时,应关注并遵守其所适用的相关条款。"),_c('br'),_vm._v(" \n    您在使用优蓝招聘提供的各项服务之前，应仔细阅读本服务协议。如您不同意本服务协议及或随时对其的修改，您可以主动取消优蓝招聘提供的服务；您一旦使用优蓝招聘服务，即视为您已了解并完全同意本服务协议各项内容，包括优蓝招聘对服务协议随时所做的任何修改，并成为优蓝招聘用户。"),_c('br'),_vm._v(" \n    二、注册信息和隐私保护 "),_c('br'),_vm._v("\n    2．1 优蓝招聘账号（即优蓝招聘用户ID）的所有权归优蓝招聘，用户完成注册申请手续后，获得优蓝招聘账号的使用权。用户应提供及时、详尽及准确的个人资料，并不断更新 注册资料，符合及时、详尽准确的要求。所有原始键入的资料将引用为注册资料。如果因注册信息不真实而引起的问题，并对问题发生所带来的后果，优蓝招聘不负任 何责任。 "),_c('br'),_vm._v("\n    2．2 用户不应将其账号转让、出售或出借予他人使用，若用户授权他人使用账户，应对被授权人在该账户下发生所有行为负全部责任。 "),_c('br'),_vm._v("\n    2．3 注册后同意优蓝招聘客服电话回访联系。 "),_c('br'),_vm._v("\n    2．4 优蓝招聘的隐私权保护声明说明了优蓝招聘如何收集和使用用户信息。您保证已经充分了解并同意优蓝招聘可以据此处理用户信息。 "),_c('br'),_vm._v("\n    三、使用规则 "),_c('br'),_vm._v("\n    3．1 用户在使用优蓝招聘服务时，必须遵守中华人民共和国相关法律法规的规定，用户应同意将不会利用本服务进行任何违法或不正当的活动，包括但不限于下列行为∶ "),_c('br'),_vm._v("\n（1）上传、展示、张贴、传播或以其它方式传送含有下列内容之一的信息： "),_c('br'),_vm._v("\n    1） 反对宪法所确定的基本原则的； "),_c('br'),_vm._v("\n    2） 危害国家安全，泄露国家秘密，颠覆国家政权，破坏国家统一的； "),_c('br'),_vm._v("\n    3） 损害国家荣誉和利益的； "),_c('br'),_vm._v("\n    4） 煽动民族仇恨、民族歧视、破坏民族团结的； "),_c('br'),_vm._v("\n    5） 破坏国家宗教政策，宣扬邪教和封建迷信的； "),_c('br'),_vm._v("\n    6） 散布谣言，扰乱社会秩序，破坏社会稳定的； "),_c('br'),_vm._v("\n    7） 散布淫秽、色情、赌博、暴力、凶杀、恐怖或者教唆犯罪的； "),_c('br'),_vm._v("\n    8） 侮辱或者诽谤他人，侵害他人合法权利的； "),_c('br'),_vm._v("\n    9） 含有虚假、有害、胁迫、侵害他人隐私、骚扰、侵害、中伤、粗俗、猥亵、或其它道德上令人反感的内容； "),_c('br'),_vm._v("\n   10） 含有中国法律、法规、规章、条例以及任何具有法律效力之规范所限制或禁止的其它内容的； "),_c('br'),_vm._v("\n（2）不得为任何非法目的而使用网络服务系统； "),_c('br'),_vm._v("\n（3）不利用优蓝招聘服务从事以下活动： "),_c('br'),_vm._v("\n    1）未经允许，进入计算机信息网络或者使用计算机信息网络资源的； "),_c('br'),_vm._v("\n    2） 未经允许，对计算机信息网络功能进行删除、修改或者增加的； "),_c('br'),_vm._v("\n    3） 未经允许，对进入计算机信息网络中存储、处理或者传输的数据和应用程序进行删除、修改或者增加的； "),_c('br'),_vm._v("\n    4） 故意制作、传播计算机病毒等破坏性程序的；"),_c('br'),_vm._v(" \n    5） 其他危害计算机信息网络安全的行为。 "),_c('br'),_vm._v("\n    3．2 用户违反本协议或相关的服务条款的规定，导致或产生的任何第三方主张的任何索赔、要求或损失，包括合理的律师费，您同意赔偿优蓝招聘与合作公司、关联公司， 并使之免受损害。对此，优蓝招聘有权视用户的行为性质，采取包括但不限于删除用户发布信息内容、暂停使用许可、终止服务、限制使用、回收优蓝招聘账号、追究法 律责任等措施。对恶意注册优蓝招聘账号或利用优蓝招聘账号进行违法活动、捣乱、骚扰、欺骗、其他用户以及其他违反本协议的行为，优蓝招聘有权回收其账号。同时， 优蓝招聘会视司法部门的要求，协助调查。 "),_c('br'),_vm._v("\n    3．3 用户不得对本服务任何部分或本服务之使用或获得，进行复制、拷贝、出售、转售或用于任何其它商业目的。 "),_c('br'),_vm._v("\n    3．4 用户须对自己在使用优蓝招聘服务过程中的行为承担法律责任。用户承担法律责任的形式包括但不限于：对受到侵害者进行赔偿，以及在优蓝招聘首先承担了因用户行为导致的行政处罚或侵权损害赔偿责任后，用户应给予优蓝招聘等额的赔偿。 "),_c('br'),_vm._v("\n    四、服务内容 "),_c('br'),_vm._v("\n    4．1 优蓝招聘网络服务的具体内容由优蓝招聘根据实际情况提供。 "),_c('br'),_vm._v("\n    4．2 除非本服务协议另有其它明示规定，优蓝招聘所推出的新产品、新功能、新服务，均受到本服务协议之规范。 "),_c('br'),_vm._v("\n    4．3 为使用本服务，您必须能够自行经有法律资格对您提供互联网接入服务的第三方，进入国际互联网，并应自行支付相关服务费用。此外，您必须自行配备及负责与国际联网连线所需之一切必要装备，包括计算机、数据机或其它存取装置。 "),_c('br'),_vm._v("\n    4．4 鉴于网络服务的特殊性，用户同意优蓝招聘有权不经事先通知，随时变更、中断或终止部分或全部的网络服务（包括收费网络服务）。优蓝招聘不担保网络服务不会中断，对网络服务的及时性、安全性、准确性也都不作担保。 "),_c('br'),_vm._v("\n    4．5 优蓝招聘需要定期或不定期地对提供网络服务的平台或相关的设备进行检修或者维护，如因此类情况而造成网络服务（包括收费网络服务）在合理时间内的中断，优蓝招聘无需为此承担任何责任。优蓝招聘保留不经事先通知为维修保养、升级或其它目的暂停本服务任何部分的权利。 "),_c('br'),_vm._v("\n    4．6 本服务或第三人可提供与其它国际互联网上之网站或资源之链接。由于优蓝招聘无法控制这些网站及资源，您了解并同意，此类网站或资源是否可供利用，优蓝招聘不予 负责，存在或源于此类网站或资源之任何内容、广告、产品或其它资料，优蓝招聘亦不予保证或负责。因使用或依赖任何此类网站或资源发布的或经由此类网站或资源 获得的任何内容、商品或服务所产生的任何损害或损失，优蓝招聘不承担任何责任。 "),_c('br'),_vm._v("\n    4．7 用户明确同意其使用优蓝招聘网络服务所存在的风险将完全由其自己承担。用户理解并接受下载或通过优蓝招聘服务取得的任何信息资料取决于用户自己，并由其承担系 统受损、资料丢失以及其它任何风险。优蓝招聘对在服务网上得到的任何商品购物服务、交易进程、招聘信息，都不作担保。 "),_c('br'),_vm._v("\n    4．8 优蓝招聘有权于任何时间暂时或永久修改或终止本服务（或其任何部分），而无论其通知与否，优蓝招聘对用户和任何第三人均无需承担任何责任。 "),_c('br'),_vm._v("\n    4．9 终止服务 "),_c('br'),_vm._v("\n    您同意优蓝招聘得基于其自行之考虑，因任何理由，包含但不限于长时间未使用，或优蓝招聘认为您已经违反 本服务协议的文字及精神，终止您的密码、账号或本服务之使用（或服务之任何部分），并将您在本服务内任何内容加以移除并删除。您同意依本服务协议任何规定 提供之本服务，无需进行事先通知即可中断或终止，您承认并同意，优蓝招聘可立即关闭或删除您的账号及您账号中所有相关信息及文件，及或禁止继续使用前述文 件或本服务。此外，您同意若本服务之使用被中断或终止或您的账号及相关信息和文件被关闭或删除，优蓝招聘对您或任何第三人均不承担任何责任。 "),_c('br'),_vm._v("\n    五、知识产权和其他合法权益（包括但不限于名誉权、商誉权）"),_c('br'),_vm._v(" \n    5．1 用户专属权利 "),_c('br'),_vm._v("\n    优蓝招聘尊重他人知识产权和合法权益，呼吁用户也要同样尊重知识产权和他人合法权益。若您认为您的知识产权或其他合法权益被侵犯，请按照以下说明向优蓝招聘提供资料∶ "),_c('br'),_vm._v("\n    请注意：如果权利通知的陈述失实，权利通知提交者将承担对由此造成的全部法律责任（包括但不限于赔偿各种费用及律师费）。如果上述个人或单位不确定网络上可获取的资料是否侵犯了其知识产权和其他合法权益，优蓝招聘建议该个人或单位首先咨询专业人士。"),_c('br'),_vm._v(" \n    为了优蓝招聘有效处理上述个人或单位的权利通知，请使用以下格式（包括各条款的序号）： "),_c('br'),_vm._v("\n    1. 权利人对涉嫌侵权内容拥有知识产权或其他合法权益和或依法可以行使知识产权或其他合法权益的权属证明； "),_c('br'),_vm._v("\n    2. 请充分、明确地描述被侵犯了知识产权或其他合法权益的情况并请提供涉嫌侵权的第三方网址（如果有）。 "),_c('br'),_vm._v("\n    3. 请指明涉嫌侵权网页的哪些内容侵犯了第2项中列明的权利。 "),_c('br'),_vm._v("\n    4. 请提供权利人具体的联络信息，包括姓名、身份证或护照复印件（对自然人）、单位登记证明复印件（对单位）、通信地址、电话号码、传真和电子邮件。 "),_c('br'),_vm._v("\n    5. 请提供涉嫌侵权内容在信息网络上的位置（如指明您举报的含有侵权内容的出处，即：指网页地址或网页内的位置）以便我们与您举报的含有侵权内容的网页的所有权人/管理人联系。 "),_c('br'),_vm._v("\n    6. 请在权利通知中加入如下关于通知内容真实性的声明： “我保证，本通知中所述信息是充分、真实、准确的，如果本权利通知内容不完全属实，本人将承担由此产生的一切法律责任。” "),_c('br'),_vm._v("\n    7. 请您签署该文件，如果您是依法成立的机构或组织，请您加盖公章。 "),_c('br'),_vm._v("\n    请您把以上资料和联络方式书面发往以下地址： "),_c('br'),_vm._v("\n    上海市宝山区长江南路180号长江软件园B421，上海优尔蓝信息科技股份有限公司，邮政编码：200439。 "),_c('br'),_vm._v("\n    5．2 对于用户通过优蓝招聘服务上传到优蓝招聘上可公开获取区域的任何内容，用户同意优蓝招聘在全世界范围内具有免费的、永久性的、不可撤销的、非独家的和完全再许可的权利和许可，以使用、复制、修改、改编、出版、翻译、据以创作衍生作品、传播、表演和展示此等内容（整体或部分），和或将此等内容编入当前已知的或以 后开发的其他任何形式的作品、媒体或技术中。 "),_c('br'),_vm._v("\n    5．3 优蓝招聘拥有本网站内所有资料的版权。任何被授权的浏览、复制、打印和传播属于本网站内的资料必须符合以下条件："),_c('br'),_vm._v(" \n    （1） 所有的资料和图象均以获得信息为目的； "),_c('br'),_vm._v("\n    （2） 所有的资料和图象均不得用于商业目的； "),_c('br'),_vm._v("\n    （3） 所有的资料、图象及其任何部分都必须包括此版权声明； "),_c('br'),_vm._v("\n    （4） APP内所有的产品、技术与所有程序均属于优蓝招聘知识产权，在此并未授权。 "),_c('br'),_vm._v("\n    （5） “优蓝招聘”及相关图形等为优蓝招聘的注册商标。 "),_c('br'),_vm._v("\n    （6） 未经优蓝招聘许可，任何人不得擅自（包括但不限于：以非法的方式复制、传播、展示、镜像、上载、下