import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/login/login' // 登录页
import UserAgreement from '@/views/UserAgreement/index' // 用户协议
import UserPrivacy from '@/views/UserPrivacy/index' // 隐私权政策
import Index from '@/views/index/index' // 首页
import MyPage from '@/views/myPage/index' // 我的页面
import AboutUs from '@/views/aboutUs/index' // 关于我们
import UserDetail from '@/views/userDetail/index' // 个人信息
import UploadIdCard from '@/views/uploadIdCard/index' // 身份证上传

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/userAgreement',
      name: 'userAgreement',
      component: UserAgreement
    },
    {
      path: '/UserPrivacy',
      name: 'UserPrivacy',
      component: UserPrivacy
    },
    {
      path: '/index',
      name: 'index',
      component: Index
    },
    {
      path: '/userPage',
      name: 'myPage',
      component: MyPage
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: AboutUs
    },
    {
      path: '/userDetail',
      name: 'userDetail',
      component: UserDetail
    },
    {
      path: "/uploadIdcard",
      name: 'uploadIdcard',
      component: UploadIdCard
    },
    {
      path: '/',
      redirect: '/login'      
    }
  ]
})



